import React, {Component} from 'react';
import styled from 'styled-components';
import axios from 'axios';

import { Row, Col, Modal, Container } from 'react-bootstrap';
import history_icon from '../assets/history.svg';
import map_icon from '../assets/map-pin.svg';
import calendar_icon from '../assets/calendar.svg';

const HomeContainer = styled.div`
  padding: 2rem 0;
`;

const UserCard = styled.div`
  height: 120px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border: 1px solid #E9E9E9;
  border-radius: 4px;
  opacity: 1;
  margin: 16px 0;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    cursor: pointer;
    box-shadow: 0px 4px 10px #16161629;
    transition-duration: 0.5s;
  }
}
`;

const UserImage = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      height: 80px;
      border-radius: 100%;
    }
`;

const UserDetails = styled.div`
    h5 {
      color: #101010;
    }

    span {
      font-size: 15px;
      opacity: 0.7;
    }
`;

const ActivityLog = styled.div`
`;

const ActivityTime = styled.div`
    font-size: 1rem;
`

const ActivityDate = styled.div`
    font-size: 0.8rem;
`

class Home extends Component{
  constructor(props) {
    super(props);
    this.state = {
      modalState: false,
      userList: [],
      currentUserData: null
    }
  }

  openModal = (userID) => {
    const { userList } = this.state;
    this.setState({ modalState: true });

    // set the user data being accessed
    let currentUserData = userList.filter(data => data.id == userID);
    // console.log(currentUserData);
    let formattedActivityPeriods = currentUserData[0].activity_periods.map((data)=>{
      let { start_time, end_time } = data;
      let startTime = start_time.split("  ");
      let endTime = end_time.split("  ");

      return {
        date: startTime[0],
        start_time: startTime[1],
        end_time: endTime[1]
      }
    });

    currentUserData.activity_periods = formattedActivityPeriods;

    this.setState({currentUserData: currentUserData});
  }

  closeModal = () => {
    this.setState({ modalState: false });
  }

  loadUserData = () => {
    return axios.get(`/GetUserList`)
    .then(res=>{
      return res.data;
    })
    .catch(err=>{
      console.log(err);
    });
  }

  componentDidMount = () => {
    // load user data
    this.loadUserData().then(data=>{
      if(data.ok == true)
        this.setState({userList: data.members});
    });
  }

  render() {
    const { modalState, userList, currentUserData } = this.state;
    console.log(currentUserData);

    return(
      <HomeContainer>
        <Row>
          {
            userList.length ? userList.map(data=>(
              <Col sm={4}>
                <UserCard onClick={() => { this.openModal(data.id) } }>
                  <Row>
                    <Col sm={3}>
                      <UserImage>
                        <img src={data.image_url}/>
                      </UserImage>
                    </Col>
                    <Col sm={9} className="user-details">
                      <UserDetails>
                        <h5>{data.real_name}</h5>
                        <span><img src={map_icon}/> {data.tz}</span>
                        <br />
                        <span><img src={history_icon}/> {data.activity_periods[data.activity_periods.length-1].end_time}</span>
                      </UserDetails>
                    </Col>
                  </Row>
                </UserCard>
              </Col>
            )) : null
          }
        </Row>
      
        
        <Modal show={modalState} onHide={this.closeModal} animation={true}>
              <Modal.Header closeButton>
                <Modal.Title>
                  {currentUserData!==null?currentUserData[0].real_name:null} Activity
                </Modal.Title>
              </Modal.Header>
              <Modal.Body>
              {
                currentUserData!==null?currentUserData.map((data)=>(
                  <Container>
                      <Row>
                        <Col sm={3}>
                          <UserImage>
                            <img src={data.image_url}/>
                          </UserImage>
                        </Col>
                        <Col sm={9}>
                          <UserDetails>
                            <h5>{data.real_name}</h5>
                            <span><img src={map_icon}/> {data.tz}</span>
                          </UserDetails>
                        </Col>
                      </Row>
                      <hr />
                      <ActivityLog>
                        <Row>
                          <Col xs={4}>
                            <h5>Activity Log</h5>
                          </Col>
                          <Col xs={8}>
                            <img src={calendar_icon} />
                          </Col>
                        </Row>
                        {
                          data.activity_periods.length?data.activity_periods.map((activity_data)=>(
                            <Row>
                              <Col>
                                <ActivityTime>
                                  {activity_data.start_time} - {activity_data.end_time}
                                </ActivityTime>
                              </Col>
                              <Col>
                                <ActivityDate>
                                  {activity_data.date}
                                </ActivityDate>
                              </Col>
                            </Row>
                          )):null
                        }
                      </ActivityLog>

                  </Container>
                )):null
              }
              </Modal.Body>
        </Modal>
          
       
      </HomeContainer>
    );
  }  
}

export default Home;